const Precio = (props) => {

    const { price, percent_change_1h, percent_change_7d, percentaje_change_24h } = props.precio;

    return (
        <div className='card text-white bg-success mb-3'>
            <div className='card-header'>Precion del Bitcoin</div>
            <div className='card-body'>
            <h4 className='card-title'> Precio Actual: ${ price }</h4>
            <div className='d-md-flex justify-content-between'>
    <p className='card-text'><span className='font-weight-bold'>Ultima hora: </span>{ percent_change_1h }%</p>
    <p className='card-text'><span className='font-weight-bold'>Ultimas 24 horas: </span>{ percent_change_7d }%</p>
    <p className='card-text'><span className='font-weight-bold'>Ultima 7 Dias: </span>{ percentaje_change_24h }%</p>
            </div>
            </div>
        </div>
    );
}

export default Precio;